<template>
  <!-- TODO: Color styling og SVG's, set color on hover and active -->
  <div class="orders-detail-info__container">
    <div class="orders-detail-info__billing-address">
      <h3 class="orders-detail-info__billing-address-title">
        {{ $t('billingAddress') }}
      </h3>
      <div class="orders-detail-info__billing-address-content">
        {{ fullName }}
      </div>
      <div class="orders-detail-info__billing-address-content">
        {{ street }}
      </div>
      <div class="orders-detail-info__billing-address-content">
        {{ address }}
      </div>
      <div class="orders-detail-info__billing-address-content">
        {{ countryName }}
      </div>
      <div class="orders-detail-info__billing-address-content">
        {{ phoneNumber }}
      </div>
    </div>

    <div class="orders-detail-info__payment-info">
      <h3 class="orders-detail-info__billing-address-title">
        {{ $t('paymentMethod') }}
      </h3>
      <div class="orders-detail-info__billing-address-content bold">
        {{ paymentInfo.title }}
      </div>
      <div
        v-if="creditCardType"
        class="orders-detail-info__billing-address-content"
      >
        <span id="card-title">{{ $t('creditCardType') }}</span> 
        <span id="card-value">{{ creditCardType }}</span>
      </div>
      <div 
        v-if="creditCardNumber"
        class="orders-detail-info__billing-address-content"
      >
        <span id="card-title">{{ $t('creditCardNumber') }}</span> 
        <span id="card-value">{{ creditCardNumber }}</span>
      </div>
      <div
        v-if="isInvoices"
        class="orders-detail-info__billing-address-print"
        @click="printOrder"
      >
        {{ $t('printOrder') }}
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "billingAddress": "Heimilisfang",
    "paymentMethod": "Greiðslumáti",
    "printOrder": "Prenta pöntun",
    "creditCardType": "Tegund korts: ",
    "creditCardNumber": "Kortanúmer: "
  },
  "en": {
    "billingAddress": "Billing address",
    "paymentMethod": "Payment method",
    "printOrder": "Print Order",
    "creditCardType": "Credit Card Type: ",
    "creditCardNumber": "Credit Card Number: "
  }
}
</i18n>

<script>
  import get from '@/util/get';
  import { COUNTRY_NAME } from '@/util/constants';

  export default {
    name: 'OrdersDetailInformation',
    props: {
      billingAddress: Object,
      paymentInfo: Object,
      subPath: String,
      isInvoices: Boolean,
    },
    computed: {
      fullName() {
        const { firstname, lastname } = this.billingAddress;
        let fullName = '';
        if (firstname && lastname) fullName = `${firstname} ${lastname}`;
        return fullName;
      },
      street() {
        return `${get(this.billingAddress, 'street', '')}`;
      },
      address() {
        const { city, postcode } = this.billingAddress;
        let address = '';
         if (city && postcode) address = `${city}, ${postcode}`;
        return address;
      },
      countryName() {
        const countryId = get(this.billingAddress, 'country_id');
        return countryId ? get(COUNTRY_NAME, `${countryId.toLowerCase()}`, 'Unknow') : '';
      },
      phoneNumber() {
        const phone = get(this.billingAddress, 'telephone');
        return phone ? `T: ${phone.charAt() ? phone.substring(4, phone.length) : phone}` : '';
      },
      creditCardType() {
        const additional = get(this.paymentInfo, 'additional');
        return additional ? Object.values(additional[0])[0] : '';
      },
      creditCardNumber() {
        const additional = get(this.paymentInfo, 'additional');
        return additional ? Object.values(additional[1])[0] : '';
      }
    },
    methods: {
      printOrder() {
        // console.log("printOrder -> printOrder", "printOrder")
      },
    }
  }
</script>

