<template>
  <!-- TODO: Color styling og SVG's, set color on hover and active -->
  <div v-if="isReady" class="account-orders-detail__wrapper">
    <div class="account-order-detail__container">
      <div>
        <div class="account-orders-detail__order-title">
          <h1 class="account-orders-detail__title">
            {{ $t("orderTitle") }}{{ ordersInfo.mainOrderIncId }}
          </h1>
          <div
            :class="[
              'account-order-detail__status',
              `color__status--${statusColor(ordersInfo.orderStatus)}`,
            ]"
          >
            {{ ordersInfo.orderStatus }}
          </div>
        </div>
        <div class="account-orders-detail__order-date">
          {{ $t("orderDate") }}: {{ ordersInfo.orderDate | formatDateTime }}
        </div>
      </div>
      <div class="account-orders-detail__print" @click="printOrder">
        <router-link
          :to="`/customer/account/orders/printOrders?id=${orderId}`"
          target="_blank"
        >
          {{ $t("printOrder") }}
        </router-link>
      </div>
    </div>

    <div class="account-orders-detail__page-container">
      <div
        v-for="(page, key) in defaultPage"
        v-bind:class="{
          'account-orders-detail__page': true,
          'account-orders-detail__mgrr15': key === 0,
          'account-orders-detail__page--unactived': page.id !== subPath,
        }"
        :key="page.id"
        @click="page.id !== subPath && redirectPage(page.id)"
      >
        {{ $t(page.value) }}
      </div>
    </div>

    <OrdersDetailInformation
      :billingAddress="billingAddress"
      :paymentInfo="paymentInfo"
      v-bind="ordersDetailProps"
    />
    <OrdersDetailSubOrders :subOrders="subOrders" v-bind="ordersDetailProps" />
    <OrdersDetailSummary
      v-if="!ordersDetailProps.isInvoices"
      :ordersSummary="ordersSummary"
      v-bind="ordersDetailProps"
    />
  </div>
</template>

<i18n>
{
  "is": {
    "orderInformation": "Upplýsingar um pöntun",
    "invoices": "Reikningar",
    "orderTitle": "Pöntun #",
    "orderDate": "Dagsetning",
    "printOrder": "Prenta pöntun"
  },
  "en": {
    "orderInformation": "Order Information",
    "invoices": "Invoices",
    "orderTitle": "Order #",
    "orderDate": "Order Date",
    "printOrder": "Print Order"
  }
}
</i18n>

<script>
import { mapActions } from "vuex";

import OrdersDetailInformation from "@/components/ViewOrdersDetail/OrdersDetailInformation";
import OrdersDetailSubOrders from "@/components/ViewOrdersDetail/OrdersDetailSubOrders";
import OrdersDetailSummary from "@/components/ViewOrdersDetail/OrdersDetailSummary";

import { getOrderInfo } from "@/services/order";

import get from "@/util/get";
import getStatusColor from "@/util/getStatusColor";

export default {
  name: "ViewMyAccountOrdersDetail",
  components: {
    OrdersDetailInformation,
    OrdersDetailSubOrders,
    OrdersDetailSummary,
  },
  data() {
    return {
      defaultPage: [
        {
          id: "view",
          value: "orderInformation",
        },
        {
          id: "invoices",
          value: "invoices",
        },
      ],
      isReady: false,
      ordersInfo: undefined,
      billingAddress: undefined,
      paymentInfo: undefined,
      subOrders: undefined,
      ordersSummary: undefined,
    };
  },
  computed: {
    orderId() {
      return get(this.$route, "params.id");
    },
    subPath() {
      return get(this.$route, "params.subPath");
    },
    ordersDetailProps() {
      return {
        isInvoices: this.$route.path.includes("invoices"),
      };
    },
  },
  methods: {
    ...mapActions(["setPrintData"]),
    statusColor(status) {
      return getStatusColor(status);
    },
    printOrder() {
      const {
        setPrintData,
        orderId,
        ordersInfo,
        billingAddress,
        paymentInfo,
        subOrders,
        ordersSummary,
      } = this;
      setPrintData({
        orderId,
        ordersInfo,
        billingAddress,
        paymentInfo,
        subOrders,
        ordersSummary,
      });
    },
    redirectPage(page) {
      const { orderId } = this;
      this.$router.push(`/customer/account/orders/${page}/id/${orderId}`);
    },
  },
  async mounted() {
    const result = await getOrderInfo(this.orderId);
    if ("success" === result.status) {
      const ordersInfo = get(result, "data", {});
      this.billingAddress = ordersInfo.billing_address;
      this.paymentInfo = ordersInfo.payment_info;
      this.subOrders = ordersInfo.suborders;
      this.ordersInfo = {
        orderStatus: ordersInfo.order_status,
        orderDate: ordersInfo.order_date,
        mainOrderIncId: ordersInfo.main_order_inc_id,
      };
      this.ordersSummary = ordersInfo.order_totals;
      this.isReady = true;
    }
  },
};
</script>

